.products {
    padding: 60px 0;

    &__box{
        width: 100%;
    }
    
    &__label{
        font-family: var(--font-second);
        font-size: 18px;
        line-height: 21px;
        text-align: center;
        text-transform: uppercase;
        color: #FFA5BA;
        
            @media screen and (min-width: 768px) { 
                font-size: 26px;
                line-height: 30px;
            }
            @media screen and (min-width: 1200px) {
                font-size: 30px;
                line-height: 34px;
                letter-spacing: 0.04em;
            }
         
    }
    
    &__main-title{
        font-family: var(--font-second);
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #D41443;
    margin-top: 15px;
    margin-bottom: 8px;
    
    @media screen and (min-width: 768px) { 
        font-size: 48px;
    line-height: 55px;
    margin-top: 18px;
    margin-bottom: 20px;
    }
    @media screen and (min-width: 1200px) {
        font-size: 58px;
    line-height: 66px;
    letter-spacing: 0.06em;
    margin-top: 16px;
    }
    
    }

    &__main-box {
        border-radius: 24px;
        position: relative;
        border-radius: 24px;
        // backface-visibility: hidden;
        // perspective: 1000px;
        // transition: 1500ms;

        margin-top: 170px;
        padding: 40px;
        min-height: 460px;

        @media screen and (min-width: 768px) {
            margin-top: 158px;
            padding: 30px;
            min-height: 540px;
        }
    
        @media screen and (min-width: 1200px) {
            margin-top: 280px;
            padding: 60px;
            min-height: 570px;
        }
    }

    &__main-box--one {
        background: #FFB8CA;
    }

    &__main-box--two {
        background: #F0D1A5;
    }
    
    &__main-box--three {
        background: #C2E297;
    }

    &__set-box {
        text-align: center;
        padding-top: 120px;
    
        @media screen and (min-width: 768px) {
            padding-top: 88px;
        }
        @media screen and (min-width: 1200px) {
            padding-top: 125px;
        }
    }

    &__title {
        font-family: var(--font-second);
        font-weight: 400;
        font-size: 20px;
        line-height: 23px;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #FFFFFF;
        margin-bottom: 70px;
        position: relative;
    
        @media screen and (min-width: 1200px) {
            font-size: 30px;
            line-height: 34px;
            
        }
        
    }

    &__dotter {
        width: 6px;
        height: 6px; 
        background-color: white;
        border-radius: 50%;
        position: absolute;
        top:210px;
        @media screen and (min-width: 768px) {
            top: 170px;
        }
        @media screen and (min-width: 1200px) {
            top: 250px;
        }
    }

    &__dotter::after,
    &__dotter::before {
        content: "";
        position: absolute;
        background-color: white;
        width: 6px;
        height: 6px; 
        border-radius: 50%;
    }
    
    &__dotter::after {
        left:-10px;
        top:50%;
        transform: translateY(-50%);
    
    }
    
    &__dotter::before {
        right: -10px;
        top:50%;
        transform: translateY(-50%);
    }

    &__text{
        font-family: var(--font-general);
        font-weight: 700;
        font-size: 14px;
        line-height: 152%;
        text-align: center;
        letter-spacing: 0.04em;
        color: #FFFFFF;
    
        @media screen and (min-width:768px) {
            font-size: 16px;
          line-height: 176%;
        }
    }
    
    &__btn-svg{
        background-color: inherit;
        border-radius: 50%;
        transition : box-shadow var(--anim-link);
        padding: 0;
        margin-top: 32px;
        cursor: pointer;
        z-index: 10;
    
        @media screen and (min-width: 1200px) {
            margin-top: 60px;
        }
    
        &:hover{
            box-shadow: 11px 0px 30px -8px rgba(0,0,0,0.75);
        }
    }
}

.products__main-box--one::after {
    content: "";
    width: 280px;
    height: 280px;
    position: absolute;
    top: -35%;
    left: 50%;
    transform: translateX(-50%);
    background-position: center;
    background-image: url(../image/2-products/ice-cream1_sm.png);
    background-repeat: no-repeat;
    background-size: cover;
    
    @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: url(../image/2-products/ice-cream1_sm@2x.png);
       
    }
    
    @media screen and (min-width: 768px){
        background-image: url(../image/2-products/ice-cream1_md.png);
        top: -25%;
        width: 224px;
        height: 224px;
    
    // Для ретіна екранів планшетна версія
    @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../image/2-products/ice-cream1_md@2x.png);
        }
    }
    
    @media screen and (min-width: 1200px) {
        background-image: url(../image/2-products/ice-cream1_xl.png);
        top: -50%;
        width:368px ;
        height: 394px;
    
    // Для ретіна екранів десктопна версія
    @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../image/2-products/ice-cream1_xl@2x.png);       
        }
    } 
}

