.products {

    &__back-card {
        display: none;
    }

    &__back-btn {
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
    }

    &__back-btn-icon {
        display: block;
        position: relative;
        width: 20px;
        height: 2px;
        background-color: var(--color-general);
        transform: rotate(-45deg);
    }
    &__back-btn-icon::before {
        content: "";
        position: absolute;
        width: 20px;
        height: 2px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(90deg);
        background-color: var(--color-general);
    }
}

.products__set-box.is-rotate {
    display: none;
}

.products__back-card.is-rotate {
    display: block;
}


