
.cardset{
    padding: 0;

    @media screen and (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        margin-left: calc(-1 * var(--gap) );
        margin-top: calc(-1 * var(--top) );
        justify-content: center;
    }


    &__item{

        @media screen and (min-width: 768px) {
            flex-basis: calc(100% / var(--card-number) - var(--gap) );
            margin-left: var(--gap);
            margin-top: var(--top);
        }        
    }
}
