.gallery {
    background: rgb(255,245,246);
    background: linear-gradient(180deg, rgba(255,245,246,1) 25%, rgba(255,255,255) 25%);
    
    &__grid {
        display: grid;
        grid-template-rows: repeat(14, 1fr);
        grid-template-columns: repeat(28, 1fr);
        gap: 5px;
    }
    &__item {
        margin: 0;
        transition-property: transform, border-radius;
        transition-duration: 250ms;
        transition-timing-function: linear;
        cursor: pointer;
        // position: relative;
    }
    // &__item:hover, 
    // &__item:focus {
    //     transform: scale(1.3);
    //     border-radius: 30px;
    // }
    &__box-img {
        transition-duration: 500ms;
        transition-property: all;
        transition-timing-function: linear;
    }

    &__box-img:hover {
        // position: absolute;
        // top: 0;
        // left: 0;
        transform: scale(2.5);
        z-index: 100;
        
    }
    &__box-img--1:hover {
        transform-origin: center left;
    }

    &__box-img--10:hover {
        transform-origin: center right;
    }

    &__box-img--4:hover {
        transform: scale(1.2);
    }

    &__item--1 {
        grid-column-start: 1;
        grid-column-end: 5;
        grid-row-start: 6;
        grid-row-end: 10;
    }
    &__item--2 {
        grid-column-start: 5;
        grid-column-end: 9;
        grid-row-start: 4;
        grid-row-end: 8;
    }
    &__item--3 {
        grid-column-start: 5;
        grid-column-end: 9;
        grid-row-start: 8;
        grid-row-end: 12;
    }
    &__item--4 {
        grid-column-start: 9;
        grid-column-end: 17;
        grid-row-start: 2;
        grid-row-end: 10;
    }
    &__item--5 {
        grid-column-start: 9;
        grid-column-end: 13;
        grid-row-start: 10;
        grid-row-end: 14;
    }
    &__item--6 {
        grid-column-start: 17;
        grid-column-end: 21;
        grid-row-start: 5;
        grid-row-end: 9;
    }
    &__item--7 {
        grid-column-start: 17;
        grid-column-end: 21;
        grid-row-start: 9;
        grid-row-end: 13;
    }
    &__item--8 {
        grid-column-start: 21;
        grid-column-end: 25;
        grid-row-start: 4;
        grid-row-end: 8;
    }
    &__item--9 {
        grid-column-start: 21;
        grid-column-end: 25;
        grid-row-start: 8;
        grid-row-end: 12;
    }
    &__item--10 {
        grid-column-start: 25;
        grid-column-end: 29;
        grid-row-start: 6;
        grid-row-end: 10;
    }
}

